import * as router from 'vue-router'

import home from '../pages/home.vue'
import about from '../pages/buy.vue'
import contact from '../pages/contact.vue'
import buyProduct from '../pages/buyProduct.vue'
import checkout from '../pages/buyCheckout.vue'

const routes = [
    { path: '/', name: 'Home', component: home },
    { path: '/buy', name: 'About', component: about },
    { path: '/contact', name: 'Contact', component: contact },
    { path: '/buy/products', name: 'BuyProducts', component: buyProduct },
    { path: '/buy/checkout', name: 'Checkout', component: checkout, query: { size: String, color: String } }
]

const myRoutes = router.createRouter({
    history: router.createWebHashHistory(),
    routes
})

export default myRoutes