
import { defineComponent } from 'vue'

export default defineComponent({
    name: 'BuyProduct',
    data() {
        return {
            chosenImage: 'Black',
            chosenSize: 'Medium'
        }
    },
    methods: {
        switchImg(oof: string) {
            this.chosenImage = oof
        },

        switchSize(size: string) {
            this.chosenSize = size
        }
    }
})
